.todo{width:600px;min-height:600px;padding:20px;border-radius:30px;box-shadow:0 0 6px 3px rgba(0,0,0,0.13)}.todo__input{width:80%;height:40px;padding:5px 15px;border-radius:23px;font-size:20px;box-shadow:0 0 6px 3px rgba(0,0,0,0.13);border:1px solid #000;outline:none;transition:0.2s}.todo__input:focus{box-shadow:0 0 9px 5px rgba(0,0,0,0.13)}.todo__add-button{display:inline-block;margin-left:20px;height:40px;width:15%;font-size:20px;font-weight:bold;background:transparent;border:3px solid black;border-radius:18px;outline:none;cursor:pointer;transition:0.2s}.todo__add-button:hover{background:#000;color:white}.todo__tasks{margin-top:20px;max-height:510px;padding:10px;overflow-y:scroll}.todo__tasks-task{min-height:40px;box-shadow:0 0 6px 3px rgba(0,0,0,0.13);border-radius:23px;margin-top:15px;padding:14px;position:relative}.todo__tasks-task:first-child{margin-top:0}.todo__tasks-task .edit,.todo__tasks-task .ready{width:25px;height:25px;position:absolute;top:10px;right:50px;background-position:center center;background-size:cover;cursor:pointer;transition:0.2s}.todo__tasks-task .edit:hover,.todo__tasks-task .ready:hover{transform:scale(1.04)}.todo__tasks-task .edit{background:url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABgAAAAYCAYAAADgdz34AAAABmJLR0QA/wD/AP+gvaeTAAAAeElEQVRIie3PQQqAIBSE4Z86WN2iTUcRestu0RHrAIEt6pGI1cJnKwcGBPEbhJozDtiAoQQugL+6A2N42RiPtcCC0U+64Czcv/DAmosrKA8jzgLXxiMSP8jBtXMO+oVr+5L4VPGK/4NTGn8bMMGfBszw1IApXpPMAZdgcqCjdpTcAAAAAElFTkSuQmCC);background-size:cover}.todo__tasks-task .ready{background:url(data:image/png;base64,);background-size:cover}.todo__tasks-task .deleate{position:absolute;top:10px;right:20px;font-size:20px;color:red;cursor:pointer;transition:0.2s}.todo__tasks-task .deleate:hover{transform:scale(1.04)}.todo__tasks-task .text{display:inline-block;font-size:17px;max-width:400px;word-break:break-word;margin:0}.todo__tasks-task textarea{padding:10px;font-size:17px;resize:none;width:80%;min-height:300px;box-shadow:0 0 6px 3px rgba(0,0,0,0.13);border-radius:10px}.todo__tasks-task .is-done{display:inline-block;width:17px;height:17px;border:1px solid;border-radius:50%;margin-right:10px;cursor:pointer;position:relative;transition:0.4s}.todo__tasks-task .is-done:before{content:"✔";position:absolute;font-size:13px;color:white;top:-1px;left:2px}.todo__tasks-task input[type="checkbox"]{display:none}.todo__tasks-task input[type="checkbox"]:checked ~ .is-done{background:#1aff16;border:1px solid #1aff16}

body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}#root{width:100%;height:100vh;display:flex;align-items:center;justify-content:center}*{box-sizing:border-box}

